import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pageState: 0,
    hasBackground: false
  },
  mutations: {
    setPageState(state, pageState) {
      if (state.pageState !== pageState) {
        state.pageState = pageState
      }
    },
    setBackground(state, backgroundState) {
      state.hasBackground = backgroundState
    }
  }
})
