import Vue from 'vue'
import Router from 'vue-router'
import AppView from '@/components/app-view.vue'

Vue.use(Router)

const page = name => () => import('@/pages/' + name)

export default new Router({
  base: process.env.BASE_URL,
  linkActiveClass: 'active',
  routes: [{
    path: '/',
    redirect: 'home',
    component: AppView,
    children: [
      {
        path: '/home',
        name: 'home',
        component: page('home/home'),
        meta: {}
      }, {
        path: '/customer',
        name: 'customer',
        component: page('customer/customer'),
        meta: {}
      },
      {
        path: '/case',
        name: 'case',
        component: page('case/case'),
        meta: {}
      },
      {
        path: '/about',
        name: 'about',
        component: page('about/about'),
        meta: {}
      },
      {
        path: '/guide',
        name: 'guide',
        component: page('guide/guide'),
        meta: {}
      },
      {
        path: '/news',
        name: 'news',
        component: page('news/news'),
        meta: {}
      }
    ]
  }]
})
