<template>
    <div class="app-header" id="app-header">
        <transition name="normal">
            <div :style="{'background-color':hasBackground? 'rgba(0, 0, 0, 0.8)':'rgba(0,0,0,0)'}" class="header-normal"
                 v-if="!searchShow && !navShow">
                <app-logo :class="handleColorChange()"></app-logo>
                <div :class="handleColorChange()" class="right-top-box clear-fix">
                    <i @click="openSearch" class="iconfont iconSerch pointer" id="search"></i>
                    <i @click="openNav" class="iconfont iconNav pointer" id="nav"></i>
                </div>
            </div>
        </transition>

        <transition name="search">
            <div class="header-search" v-click-outside="close" v-if="searchShow">
                <div class="right-top-box font-f">
                    <i @click="close" class="iconfont iconClose pointer"></i>
                </div>
                <div class="content">
                    <el-input placeholder="Search..." v-model="searchText">
                        <i @click="alertTip" class="iconfont iconSerch pointer" slot="suffix"></i>
                    </el-input>
                </div>
            </div>
        </transition>

        <transition name="nav">
            <div class="header-nav" v-click-outside="close" v-if="navShow">
                <div class="right-top-box font-f">
                    <i @click="close" class="iconfont iconClose pointer"></i>
                </div>
                <ul class="content">
                    <li :class="{isActive:selectionNavIndex === index}" @click="handleSelectionChange(index,item)"
                        class="pointer"
                        v-for="(item,index) in navContent">
                        <p>{{item.title}}</p>
                        <div class="decorate"></div>
                        <p>{{item.english}}</p>
                        <p>{{item.chinese}}</p>
                    </li>
                </ul>
            </div>
        </transition>
    </div>
</template>

<script>
  import appLogo from './app-logo'

  export default {
    name: 'app-header',
    components: {appLogo},
    data() {
      return {
        searchShow: false,
        navShow: false,
        searchText: '',
        selectionNavIndex: '',
        navContent: [
          {
            title: 'H',
            english: 'Home',
            chinese: '首页',
            path: '/home'
          }, {
            title: 'A',
            english: 'Advertising Design',
            chinese: '导视设计',
            path: '/guide'
          }, {
            title: 'C',
            english: 'Customer Case',
            chinese: '客户案例',
            path: '/customer'
          }, {
            title: 'N',
            english: 'News Information',
            chinese: '新闻动态',
            path: '/news'
          }, {
            title: 'A',
            english: 'About Us',
            chinese: '关于我们',
            path: '/about'
          }
        ]
      }
    },
    mounted() {
      this.selectionNavIndex = this.navContent.findIndex(item => this.$route.fullPath.indexOf(item.path) > -1)
    },
    methods: {
      handleColorChange() {
        return (this.pageState === 0 || this.pageState === 4) ? 'font-f' : 'font-0'
      },
      handleSelectionChange(index, item) {
        this.selectionNavIndex = index
        this.$store.commit('setPageState', 0)
        this.$router.push({path: item.path})
      },
      openNav() {
        this.navShow = true
      },
      openSearch() {
        this.searchShow = true
      },
      close() {
        console.log('关闭')
        this.searchText = ''
        this.searchShow = false
        this.navShow = false
      },
      alertTip() {
        if (this.searchText === '') {
          this.$message({
            showClose: true,
            message: '搜索内容不能为空',
            type: 'warning'
          })
        } else {
          this.$message({
            showClose: true,
            message: '此功能尚未实现'
          })
        }
      }
    },
    computed: {
      pageState() {
        return this.$store.state.pageState
      },
      hasBackground() {
        return this.$store.state.hasBackground
      }
    },
    watch: {
      $route(to, from) {
        this.selectionNavIndex = this.navContent.findIndex(item => to.fullPath.indexOf(item.path) > -1)
      }
    }
  }
</script>

<style lang="less">
    .app-header {
        width: 100%;
        position: absolute;
        top: 0;
        left: 0;
        z-index: 20;

        .header-normal {
            width: 100%;
            height: 80px;
        }

        .right-top-box {
            position: absolute;
            top: 23px;
            right: 60px;
            z-index: 20;

            i {
                margin-left: 1.4rem;
                font-size: 32px;
            }

        }

        .header-search {
            position: absolute;
            top: 0;
            width: 100%;
            height: 15rem;
            background-color: rgba(0, 0, 0, 0.8);
            .content {
                margin: 6rem auto;
                height: 3rem;
                width: 30rem;

                .el-input__inner {
                    background-color: transparent;
                    height: 3rem;
                    line-height: 3rem;
                    border: 1.5px solid #fff;
                    border-radius: 0.4rem;
                    font-size: 0.8rem;
                    color: rgba(255, 255, 255, 1);
                }


                i {
                    margin: 0.6rem;
                    font-size: 1.6rem;
                    line-height: 3rem;
                    color: white;
                }
            }
        }

        .header-nav {
            position: absolute;
            top: 0;
            width: 100%;
            height: 15rem;
            background-color: rgba(0, 0, 0, 0.8);

            .content {
                color: white;
                margin: 4rem auto;
                display: flex;
                flex-flow: row nowrap;
                justify-content: space-between;
                text-align: center;
                width: 70%;

                li {
                    /*width: 110px;*/
                    font-size: 5.5rem;
                    list-style-type: none;
                    opacity: 0.6;

                    .decorate {
                        display: none;
                    }
                }

                .isActive {
                    opacity: 1;
                    .decorate {
                        display: block;
                        background: white;
                        width: 2rem;
                        height: 0.1rem;
                        margin: 0.15rem auto;
                    }
                    p {
                        &:nth-child(1n) {
                            font-size: 3.2rem;
                            font-weight: 600;
                        }

                        &:nth-child(3n) {
                            font-size: 0.8rem;
                            margin-top: 0.55rem;
                        }

                        &:nth-child(4n) {
                            font-size: 1rem;
                            margin-top: 0.2rem;
                        }
                    }

                }

                p {
                    &:nth-child(1n) {
                        font-size: 3.2rem;
                        font-weight: 600;
                    }

                    &:nth-child(3n) {
                        font-size: 0.8rem;
                        margin-top: 0.8rem;
                    }

                    &:nth-child(4n) {
                        font-size: 1rem;
                        margin-top: 0.2rem;
                    }
                }

            }
        }


        // header退出进入渐变
        .normal-leave-active {
            transition: opacity .3s ease;
        }

        .normal-enter-active {
            transition: opacity .3s ease .5s;
        }

        .normal-enter, .normal-leave-to {
            opacity: 0;
        }

        .nav-enter, .nav-leave-to, .search-enter, .search-leave-to {
            top: -15rem;
        }

        .nav-leave-active, .search-leave-active {
            transition: top .5s ease;
        }

        .nav-enter-active, .search-enter-active {
            transition: top .5s ease .2s;
        }

        // 字体颜色
        .font-f {
            transition: color 1s ease;
            color: #fff;
        }

        .font-0 {
            transition: color 1s ease;
            color: #000;
        }
    }
</style>
